import Modal from "react-bootstrap/Modal";
import React, { useState } from 'react';
import Button from 'react-bootstrap/Button';
import { useRef } from "react";

function ConfirmDialogButton(props) {
  const [showDialog, setShowDialog] = useState(false);
  const resolver = useRef();


  const handleOnClick = async () => {
    const result = await handleShow();
    result && props.onClick();
  };

  const handleShow = () => {
    setShowDialog(true);

    return new Promise(function (resolve) {
      resolver.current = resolve;
    });
  };

  const handleOk = () => {
    resolver.current && resolver.current(true);
    setShowDialog(false);
  };

  const handleCancel = () => {
    resolver.current && resolver.current(false);
    setShowDialog(false);
  };

  const handleHide = () => {
    setShowDialog(false);
    console.log(showDialog)
  }



  return (<>
  <Button variant={props.variant} style={props.style} onClick={()=>handleOnClick()} disabled={props.disabled}>
    {props.children}
    </Button>

      <Modal show={showDialog} onHide={handleHide} animation={false} >
        <Modal.Header closeButton>
          <Modal.Title>{props.title}</Modal.Title>
        </Modal.Header>
        <Modal.Body>{props.body}</Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleCancel}>
            Cancel
          </Button>
          <Button variant="primary" onClick={handleOk}>
            Accept
          </Button>
        </Modal.Footer>
      </Modal>
  </>



  );
}

export default ConfirmDialogButton;
