import {useContext} from "react"
import BootstrapNavbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";
import logo from "./logo.png";
import { Link } from "react-router-dom";
import NavDropdown from "react-bootstrap/NavDropdown";
import LanguageSelector from '../../utils/LanguageSelector';
import { Text, LanguageContext } from '../../utils/Language';

function Navbar(props) {
  const { dictionary } = useContext(LanguageContext);
  const user = props.user;
  if (user && user.UserId) {
    return (
      <>
        <BootstrapNavbar
          sticky="top"
          collapseOnSelect
          expand="md"
          bg="light"
          variant="light"
        >
          <BootstrapNavbar.Brand as={Link} to="/patients">
            <img
              src={logo}
              height="50"
              className="d-inline-block align-top"
              alt="Dentaline logo"
            />
          </BootstrapNavbar.Brand>
          <BootstrapNavbar.Toggle aria-controls="responsive-navbar-nav" />
          <BootstrapNavbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
              <Nav.Link as={Link} to="/patients">
                <Text tid="Patients"/>
              </Nav.Link>
            </Nav>
            <Nav>
              <NavDropdown      
                title={dictionary.Settings}
                className="mx-3"
                id="collasible-nav-dropdown"
              >
                <NavDropdown.Item as={Link} to="/profile"><Text tid="Profile"/></NavDropdown.Item>
                <NavDropdown.Item href="#"><Text tid="Support"/></NavDropdown.Item>
                <NavDropdown.Divider />
                <NavDropdown.Item as={Link} to="/logout">
                <Text tid="Logout"/>
                </NavDropdown.Item>
              </NavDropdown>
              </Nav>
              <Nav>
              <LanguageSelector></LanguageSelector>
            </Nav>
          </BootstrapNavbar.Collapse>
        </BootstrapNavbar>
      </>
    );
  }
}

export default Navbar;
