import { MAIN_URL } from "./ServiceConstants";
import axios from 'axios'

async function upsertClinic (Action, Id, formDataObj){
return axios.post(MAIN_URL + "/OrthoMagic/SetPortalClinic", {dataobject: {Action, Id, ...formDataObj}})
};

function getClinicsList(postData) {
    return axios.post(MAIN_URL + "/OrthoMagic/SetPortalClinic", {dataobject: {"Action":"S"}});
};

function deleteClinic(clinicId) {
    return axios.post(MAIN_URL + "/OrthoMagic/SetPortalClinic", {dataobject: {"Action":"D", "Id": clinicId}});
}
  
const DoctorService = {upsertClinic,getClinicsList, deleteClinic}

export default DoctorService