import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Card from "react-bootstrap/Card";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Image from "react-bootstrap/Image";
import A from "./a.jpg";
import Stack from "react-bootstrap/Stack";
import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import Button from 'react-bootstrap/Button';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import Tooltip from 'react-bootstrap/Tooltip';
import { useNavigate } from "react-router-dom";
import { useLocation } from "react-router-dom";
import ConfirmDialog from "../../common/ConfirmDialog";
import { Spinner } from "react-bootstrap";
import { PatientService } from "../../../services";

function timeout(delay) {
  return new Promise(res => setTimeout(res, delay));
}

function ActionDetail(props) {
  const [conversation, setConversation] = useState([]);
  const [simLink, setSimLink] = useState("");
  const [images, setImages] = useState([]);
  const params = useParams();
  const { state } = useLocation();
  const [note, setNote] = useState("");
  const navigate = useNavigate();
  const [showConfirm, setShowConfirm] = useState(false);
  const [confirmTitle, setConfirmTitle] = useState("");
  const [confirmBody, setConfirmBody] = useState("");
  const [acceptData, setAcceptData] = useState({});
  const [showSpinner, setShowSpinner] = useState(false);

  const onAccept = () => {
    console.log("onAccept");
    console.log(acceptData);

    if (acceptData.type == "revision") {
      setShowSpinner(true);
      console.log("send to revision: ", state.actionId)
      sendToRevision(state.actionId)
        .then((data) => {
          return data.json();
        }).then((data) => {
          console.log(data);
          return timeout(3000);
        }).finally(() => {
          console.log("finally");
          setShowSpinner(false);
          navigate(-1)
        })
    } else if (acceptData.type == "approval") {
      setShowSpinner(true);
      console.log("approved: ", state.actionId)
      approvePlan(state.actionId)
        .then((data) => {
          return data.json();
        }).then((data) => {
          console.log(data);
          return timeout(3000);
        }).finally(() => {
          setShowSpinner(false);
          navigate(-1)
        })
    }
  }

  const onCancel = () => {
    setAcceptData({});
    setShowConfirm(false);
  }

  async function sendToRevision(actionId) {
    return PatientService.sendToRevision(actionId, note)
  }

  async function approvePlan(actionId) {
    return PatientService.approvePlan(actionId, note)
  }

  useEffect(() => {
    let cargoId = "";
    props.updateSequence();
    PatientService.getPlanDetail(params.Id)
      .then((json) => {

        const firstElement = json.shift();
        setSimLink(firstElement.SimLink);
        cargoId = firstElement.CargoId;
        setConversation(json.reverse());

        console.log("new cargo id: ", cargoId);
         PatientService.getImageSet(cargoId)
          .then((json) => {
            const imgs = [];
            Object.keys(json[0]).map((i) => {
              if (i.includes("Img") == true) {
                if (json[0][i])
                  imgs.push(json[0][i]);
              }

            });

            console.log(imgs);
            setImages(imgs);
          })
      });

    return () => { };
  }, []);

  return (<>
    <Container fluid>
      <Row>
        {/* <Col  className="min-vh-100" lg style={{ background: "red" }}> */}
        <Col className="d-none d-md-block" lg={2} style={{ background: "#383737", position: "relative", height: "100vh" }}>
          {/* <Col lg={2} > */}
          <Stack gap="1" style={{ "overflowY": "auto", height: "100%" }}>
            {images.map((item) => {
              const imageURL = `http://144.91.100.115:8444/OrthoMagic/GetImage?ImageId=TN-${item}&GUID=${props.user.SessionGUID}&UserId=${props.user.UserId}`
              return (<Image key={item} fluid src={imageURL} />)
            })}

          </Stack>

        </Col>
        <Col lg>
          {/* <Col lg style={{ background: "green" }}> */}
          <div className="embed-responsive embed-responsive-4by3">
            {<iframe
              className="min-vh-100 col-lg-12 col-md-12 col-sm-12"
              src={simLink}
            ></iframe>}
          </div>
        </Col>
        <Col lg={3} className="min-vh-100 d-none d-md-block" style={{ position: "relative" }}>
          {/* <Col lg={3}> */}
          <Stack gap="1" style={{ "overflowY": "auto", height: "70vh" }}>
            {conversation.map((item) => {
              const isDoctor = props.user.UserId == item.UserId;
              const userName = isDoctor ? "Doctor" : "OrthoMagic";
              return (
                <Card key={item.Id}>
                  <Card.Header>
                    <OverlayTrigger
                      placement="left"
                      overlay={
                        <Tooltip className="d-none d-lg-block">
                          {item.SystemDate}
                        </Tooltip>
                      }
                    >
                      <Stack direction="horizontal">
                        <div>{userName}</div>
                        <div className="ms-auto text-muted d-none d-lg-block">
                          {item.SystemDate.split(" ")[0]}
                        </div>
                        <div className="ms-auto text-muted d-lg-none">
                          {item.SystemDate}
                        </div>
                      </Stack>
                    </OverlayTrigger>
                  </Card.Header>
                  <Card.Body>{item.Note}</Card.Body>
                </Card>
              );
            })}
            <Form style={{
              justifyContent: 'center',
              alignItems: 'center', "position": 'absolute', left: "0px",
              "bottom": '10px', "right": '0px'
            }}>
              {!(state.readonly) && <div>
                <Form.Group className="mb-3">
                  <Form.Label>Write a message</Form.Label>
                  <Form.Control as="textarea" rows={3} onChange={(e) => setNote(e.target.value)} />
                </Form.Group>
                <Button
                  variant="primary"
                  className="w-100"
                  onClick={() => {
                    setConfirmTitle("Send To Revision");
                    setConfirmBody("Are you sure you want to send this plan to update according to your requests.")
                    setShowConfirm(true);
                    setAcceptData({ type: "revision" })
                  }}>
                  Send to revision
                </Button>
                <Button variant="success" className="w-100"
                  onClick={() => {
                    setConfirmTitle("Approve Plan");
                    setConfirmBody("Are you sure you want to approve this plan? It will send to production.")
                    setShowConfirm(true);
                    setAcceptData({ type: "approval" })
                  }}>
                  Approve plan
                </Button>
              </div>

              }
              <Button variant="danger" className="w-100" onClick={() => navigate(-1)}>
                Go back
              </Button>
            </Form>


          </Stack>
        </Col>
      </Row>
      <ConfirmDialog
        show={showConfirm}
        messageTitle={confirmTitle}
        messageBody={confirmBody}
        handleCancel={onCancel}
        handleAccept={onAccept}></ConfirmDialog>
      {showSpinner && <Spinner animation="border" role="status" style={{
        position: 'absolute', left: '50%', top: '50%'
      }}>
        <span className="visually-hidden">Loading...</span>
      </Spinner>}


    </Container>

  </>
  );
}

export default ActionDetail;
