export default function Login({ setUser }) {
  const [password, setPassword] = useState();
  const [username, setUserName] = useState();
  const [message, setMessage] = useState("");
  const redirect = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    const user = await LoginService.login(username, password);
    setUser(user);
    if (user.LoginName !== null) {
      setMessage("Welcome!");
      redirect("/patients");
    } else {
      setMessage("Username or password is wrong.");
    }
  };
  return (
    <div>
      <div className="container">
        <div className="d-flex align-items-center justify-content-center" style={{"height":"100vh"}}>
          <div className="col-md-6 float-end">
            <Form onSubmit={handleSubmit}>
              <div className="px-3">
                <div className="vstack">
                <img
              src={logo}
              width="30%"
              style={{ alignSelf: 'center' }}
              alt="Orthomagic Logo"
            />
                <h3 className="text-center">Doctor Portal Login</h3>
                </div>

                <Form.FloatingLabel
                  className="mb-3"
                  label="Username"
                  controlId="username"
                >
                  <Form.Control
                    type="text"
                    placeholder="Username"
                    onChange={(e) => setUserName(e.target.value)}
                  />
                </Form.FloatingLabel>
                <Form.FloatingLabel
                  className="mb-3"
                  label="Password"
                  controlId="password"
                >
                  <Form.Control
                    type="password"
                    placeholder="Password"
                    onChange={(e) => setPassword(e.target.value)}
                  />
                </Form.FloatingLabel>
                <Button className="w-100" variant="primary" type="submit">
                  Login
                </Button>
                <text className="text-center">  New doctor? Please register from <Link to="/register"> here </Link>.</text> 
                <br/>
                <text className="text-center">  Forgot your password? Reset your password <Link to="/forgotpassword"> here </Link>.</text> 
                {message && (
                  <div className="my-3 text-center rounded py-2 bg-danger text-white">
                    {message}
                  </div>
                )}
              </div>
            </Form>
          </div>
        </div>
      </div>
    </div>
  );
}

Login.propTypes = {
  setUser: PropTypes.func.isRequired,
};
