import Container from "react-bootstrap/Container";
import { useParams } from "react-router-dom";
import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import { Image } from "react-bootstrap";
import { Row, Col } from "react-bootstrap";
import { Form } from "react-bootstrap";
import logo from '../../PatientList/logo.png'
import { Link } from "react-router-dom";
import Button from "react-bootstrap/Button";
import { PatientService } from "../../../services";

function ActionKargoDetail(props) {
  const [images, setImages] = useState({})
  const params = useParams();
  const { state } = useLocation();

  const downloadFile = (fileUrl, filename) => {
    console.log("file save started")
    PatientService.downloadFile(fileUrl)
  .then((response)=>{
    console.log("download completed")
     // create file link in browser's memory
     const href = URL.createObjectURL(response);
 
     // create "a" HTML element with href to file & click
     const link = document.createElement('a');
     link.href = href;
     link.setAttribute('download', filename); //or any other extension
     document.body.appendChild(link);
     link.click();
 
     // clean up "a" element & remove ObjectURL
     document.body.removeChild(link);
     URL.revokeObjectURL(href);
 })

 console.log("file save ended")
 }
 

  useEffect(() => {
    const cargoId = params.Id.split("-")[0]
  
    PatientService.getImageSet(cargoId)
    .then(json => {
            const imgs = {};
            Object.keys(json[0]).map((i)=> {
              if (i.includes("Img") == true) {      
                if (json[0][i]) {
                  const img = `http://144.91.100.115:8444/OrthoMagic/GetImage?ImageId=TN-${json[0][i]}&GUID=${props.user.SessionGUID}&UserId=${props.user.UserId}`;
                  const img_download = `http://144.91.100.115:8444/OrthoMagic/GetImage?ImageId=${json[0][i]}&GUID=${props.user.SessionGUID}&UserId=${props.user.UserId}`;
                  imgs[i] = img;
                  imgs[i + "_download"] = img_download;
                }
                 
              }

            });

            setImages(imgs);
          });         
    
    return () => {};
  }, []);

  return (
  <Container className="col-12 col-md-9 mt-5">
    <div>
    <Row><h3>Stl File Upper Arch:</h3>
       <Col className="col-12 col-md-9">   <Link onClick={()=>downloadFile(images["Img11_download"], "upper_arch.stl")}> Download Upper Arch</Link> </Col>
       </Row>
      <Row><h3>Stl File Lower Arch:</h3>
       <Col className="col-12 col-md-9">    <Link onClick={()=>downloadFile(images["Img12_download"], "lower_arch.stl")}> Download Lower Arch </Link> </Col>
       </Row>
    </div>
  
        <Row className="mt-5"><h3>Intraoral Images:</h3></Row>
      <Row>
        <Col className="col-12 col-md-3 p-2">   <Image height={"300px"} width={"100%"} src={images['Img1']}  />  </Col>
        <Col className="col-12 col-md-3 p-2">   <Image height={"300px"} width={"100%"}  src={images["Img2"]}  />  </Col>
        <Col  className="col-12 col-md-3 p-2">   <Image height={"300px"} width={"100%"}  src={images["Img3"]}  />    </Col>
      </Row>
      <Row>
        <Col className="col-12 col-md-3 p-2">   <Image height={"300px"} width={"100%"} src={images["Img4"]}  /> </Col>
        <Col className="flex-md-wrap align-items-center d-none d-md-flex text-middle col-12 col-md-3 p-2">   <Image width={"100%"}  alt="" className="Dropzone-img d-inline m-auto" src={logo} fluid /></Col>
        <Col className="col-12 col-md-3 p-2">    <Image height={"300px"} width={"100%"}   src={images["Img5"]} /> </Col>
      </Row>
      <Row>
        <Col className="col-12 col-md-3 p-2">   <Image height={"300px"} width={"100%"}   src={images["Img6"]}  />  </Col>
        <Col className="col-12 col-md-3 p-2">    <Image height={"300px"} width={"100%"}   src={images["Img7"]}  />   </Col>
        <Col className="col-12 col-md-3 p-2">    <Image height={"300px"} width={"100%"}   src={images["Img8"]}  /> </Col>
      </Row>
      <Row className="mt-5"><h3>Panoramic Image:</h3></Row>
      <Row> <Col className="col-12 col-md-6">   <Image height={"300px"} width={"100%"} src={images["Img9"]}  />   </Col></Row> 
      <Row className="mt-5"><h3>Sefalometric Image:</h3></Row>
      <Row><Col className="col-12 col-md-6">    <Image  height={"300px"} width={"100%"} src={images["Img10"]}  />  
      </Col></Row> 

    </Container>
  );
}

export default ActionKargoDetail;
