import { useContext, useEffect, useState } from "react";
import Table from "react-bootstrap/Table";
import Tab from "react-bootstrap/Tab";
import Tabs from "react-bootstrap/Tabs";
import { useNavigate } from "react-router-dom";
import Modal from "../Login/Modal/Modal";
import "./PatientList.css";
import Figure from "react-bootstrap/Figure";
import Favourite from "../PatientDetail/Favourite";
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import { Link } from "react-router-dom";
import BootstrapTable from 'react-bootstrap-table-next';
import { Container } from "react-bootstrap";
import Image from "react-bootstrap/Image";
import ToolkitProvider, { Search } from 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit';
import { Text, LanguageContext } from '../../utils/Language';
import {PatientService, LoginService} from "../../services"

function PatientList(props) {
  const [patientList, setPatientList] = useState([]);
  const navigate = useNavigate();
  const { dictionary } = useContext(LanguageContext);

  useEffect(() => {
    PatientService.getPatientList()
    .then((response) => setPatientList(response));

    
    return () => {};

  }, []);

  function formatItem(item, index) {
    let newItem = {}
    newItem['id'] = item.Id
    newItem['fav'] = item.Favori 
    newItem['imageurl'] =  LoginService.generateImageUrl("TN-" + item.imgURL)
    newItem['name'] = item.FamilyName + " " + item.FirstName + " (" + item.PatientCode + ")"
    newItem['date'] = item.SystemDate.split(" ")[0]
    newItem['status'] = item.State
    return newItem
  }

  const { SearchBar, ClearSearchButton } = Search;
  const columns = [{
    dataField: 'fav',
    text: '',
    formatter: favFormatter
  }, {
    dataField: 'imageurl',
    text: '',
    formatter: imageFormatter
  }, {
    dataField: 'name',
    text: dictionary.PatientName,
    sort: true
  }, {
    dataField: 'date',
    text: dictionary.Date,
    sort: true
  }, {
    dataField: 'status',
    text: dictionary.Status
  }];

  const rowEvents = {
    onClick: (e, row, rowIndex) => {
      navigate("/patients/" + row['id'])
    },
  };

  function imageFormatter(cell) {
    return (
      <Image src={cell} width={30} fluid />

    );
  }

  function favFormatter(cell) {
    return (
      <Favourite fav={cell} />
    );
  }


  function render_table(num) {
    let data = patientList
      .filter((item) => item.Arsiv === num)
      .sort((a, b) => {
        if (a.PatientCode > b.PatientCode) return -1;
        else if (a.PatientCode < b.PatientCode) return 1;
        else return 0;
      })
      .map(formatItem)

    return <ToolkitProvider
      keyField='id' data={data} columns={columns}
     
      search
    >
      {
        props => (
          <div>
            <Row className="p-2" style={{ width: '%100' }}>
              <Col>            <SearchBar placeholder={dictionary.Search} {...props.searchProps} /></Col>
              <Col className="col-6" style={{ textAlign: 'right' }}><Link to="/newpatient/new" className="btn btn-primary"><Text tid="New Patient" /></Link>
              </Col>
            </Row>

            <BootstrapTable striped
              hover
              condensed
              rowEvents={ rowEvents }
              {...props.baseProps}
            />
          </div>
        )
      }
    </ToolkitProvider>
  }

  function render_tab(tabName, archiveNum) {
    const numOfItems = patientList.filter(
      (item) => item.Arsiv === archiveNum
    ).length;
    const disabled = numOfItems === 0;

    return (
      <Tab
        eventKey={archiveNum}
        disabled={disabled}
        title={`${tabName} (${numOfItems})`}
      >
        {render_table(archiveNum)}
      </Tab>
    );
  }
  return (
    <Container>
      <Tabs
        defaultActiveKey="0"
        id="tab-control"
        className="mb-3"
      >
        {render_tab(dictionary.InTreatment, "0")}
        {render_tab(dictionary.ActionRequired, "1")}
        {render_tab(dictionary.Archieved, "5")}
      </Tabs>
    </Container>
  );
}

export default PatientList;
