const Favourite = (props) => {
  let color;
  let star = "fa-star";
  const fav = props.fav;
  if (fav === "0") {
    star = "fa-star-o";
  } else if (fav === "1") {
    color = "blue";
  } else if (fav === "2") {
    color = "red";
  } else if (fav === "3") {
    color = "black";
  }

  return <i style={{ color: color }} className={`fa ${star}`}></i>;
};

export default Favourite;
