import { Navigate } from "react-router-dom";
import Navbar from "../components/Navbar/Navbar";

export default function ProtectedPage({ user, showNavbar, children }) {
  if (!user) {
    console.log(user)
    return <Navigate to="/login" replace />;
  } else if (showNavbar === true)
  return  <div>
  <Navbar user={user} />
  {children}
  </div> 
  else {
    return children
  }

}
