import "./App.css";
import "bootstrap/dist/css/bootstrap.min.css";
import { useSessionStorage } from "./utils/useSessionStorage";
import { Route, Routes } from "react-router-dom";
import * as Pages from "./pages/";
import { useState } from "react";
import { LanguageProvider } from './utils/Language';


function App() {
  const [user, setUser] = useSessionStorage("user", null);
  const [sequence, setSequence] = useState(0);
  function updateSequence() {
    setSequence(sequence + 1);
  }
  function setSessionExpired() {
    setUser(null);
  }
  return (
    <LanguageProvider>
    <div className="container-fluid App">
      <Routes>
        <Route
          path="/"
          element={
            <Pages.ProtectedPage user={user} showNavbar={true}>
              <Pages.Home user={user} />
            </Pages.ProtectedPage>
          }
        />
        <Route
          path="/products"
          element={
            <Pages.ProtectedPage user={user}>
              <Pages.Products user={user} />
            </Pages.ProtectedPage>
          }
        />
        <Route
          path="/patients"
          element={
            <Pages.ProtectedPage user={user} showNavbar={true}>
              <Pages.PatientList
                user={user}
                setSessionExpired={setSessionExpired}
                updateSequence={updateSequence}
                sequence={sequence}
              />
            </Pages.ProtectedPage>
          }
        />
        <Route
          path="/patients/:Id"
          element={
            <Pages.ProtectedPage user={user} showNavbar={true}>
              <Pages.PatientDetail
                user={user}
                setSessionExpired={setSessionExpired}
                updateSequence={updateSequence}
                sequence={sequence}
              />
            </Pages.ProtectedPage>
          }
        />
        <Route
          path="/action/:Id"
          element={
            <Pages.ProtectedPage user={user} showNavbar={false}>
              <Pages.ActionDetail
                user={user}
                setSessionExpired={setSessionExpired}
                updateSequence={updateSequence}
                sequence={sequence}
              />
            </Pages.ProtectedPage>
          }
        />
        <Route
          path="/actionkargo/:Id"
          element={
            <Pages.ProtectedPage user={user} showNavbar={false}>
              <Pages.ActionKargoDetail
                user={user}
                setSessionExpired={setSessionExpired}
                updateSequence={updateSequence}
                sequence={sequence}
              />
            </Pages.ProtectedPage>
          }
        />
        <Route path="/team" element={<p>public page: team</p>} />
        <Route path="/login" element={<Pages.LoginPage setUser={setUser} />} />
        <Route path="/register" element={<Pages.RegisterPage/>} />
        <Route path="/forgotpassword" element={<Pages.ForgotPasswordPage/>} />
        <Route
          path="/logout"
          element={<Pages.LogoutPage logoutUser={setSessionExpired} />}
        />
                <Route
          path="/profile"
          element={
            <Pages.ProtectedPage user={user} showNavbar={true}>
              <Pages.ProfilePage
                user={user}
                setSessionExpired={setSessionExpired}
                updateSequence={updateSequence}
                sequence={sequence}
              />
            </Pages.ProtectedPage>
          }
        />
         <Route
          path="/newpatient/:id"
          element={
            <Pages.ProtectedPage user={user} showNavbar={true}>
              <Pages.NewPatientPage
                user={user}
                setSessionExpired={setSessionExpired}
                updateSequence={updateSequence}
                sequence={sequence}
              />
            </Pages.ProtectedPage>
          }
        />
        <Route path="*" element={<Pages.NotFound />} />
      </Routes>

    </div>
    </LanguageProvider>
  );
}

export default App;
